import React, { useState } from 'react'
import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import Layout from './compmnents/Layout/Layout';
import Home from './compmnents/Home/Home';
import Register from './compmnents/Register/Register';
import Login from './compmnents/Login/Login';
import NotFound from './compmnents/NotFound/NotFound';
import jwt from 'jwt-decode';
import Cart from './compmnents/Cart/Cart';
import ProtectedRouting from './compmnents/ProtectedRouting/ProtectedRouting';
import Products from './compmnents/Products/Products';
import ProductDetails from './compmnents/ProductDetails/ProductDetails';
import { CounterContextProvider } from './compmnents/Context/CounterStore';
import { CartContextProvider } from './compmnents/Context/CartStore';
import { ToastContainer } from 'react-toastify';
import SendCode from './compmnents/SendCode/SendCode';
import ChangePassword from './compmnents/ChangePassowrd/ChangePassword';



export default function App() {

  let [user,setUser] = useState(null);
  function saveCurrentUser(){
      let token = localStorage.getItem('userToken');
      let decoded = jwt(token);
      setUser(decoded);
  }



  let routers = createBrowserRouter([
    {path:'',element:<Layout user={user} setUser={setUser} />,
    children:[
      {index:true, element:<Home />},
      {path:'register',element:<Register />},
      {path:'sendcode',element:<SendCode />},
      {path:'ChangePassword',element:<ChangePassword />},
      {path:'cart',element:<ProtectedRouting> <Cart /> </ProtectedRouting>},
      {path:'login',element:<Login saveCurrentUser={saveCurrentUser} />},
      {path:'products',element:<Products />},
      {path:'product/:slug',element:<ProductDetails />},
      {path:'*',element:<NotFound />},
    ]
  }
  ])

  return (
   
    <CartContextProvider>
      <ToastContainer position='top-center'/>
      <RouterProvider router={routers}></RouterProvider>
    </CartContextProvider>
  )
}
