import axios from 'axios'
import { Formik, useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { object } from 'yup';
import * as Yup from 'yup';

export default function ChangePassword() {

  let [errors,setErrors] = useState([]);
  let [statusError,setstatusError] = useState('');
  let navigate = useNavigate();
  const schema = Yup.object({
    forgetCode:Yup.string().required('code is required'),
    email:Yup.string().required('email is required').email('not vallid email'),
    password:Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{3,7}$/),
    cPassword:Yup.string().required('confirm passowrd is required').oneOf([Yup.ref('password')],'not match password')
  });

  let formik = useFormik({
    initialValues:{
      forgetCode:'',
      email:'',
      password:'',
      cPassword:'',
    },validationSchema:schema,
    onSubmit:changePassword,

  })

  async function changePassword(values){
    let {data} = await axios.patch('https://ecommerce-node4-five.vercel.app/auth/forgotPassword',values)
    .catch((err)=>{
      setstatusError(err.response.data.messgae);
    });

    if(data.message=='Done'){
        setErrors([]);
        setstatusError('');
        navigate('/login');
    }
    
    else{
      setErrors(data.err[0])
    }


  }
  return (
    <>
    
   <div className="w-75 m-auto my-5">

    <h2>Change Password</h2>
    
    {errors.map((error)=>{  
     return <p className="alert alert-danger">{error.message}</p>
    })}

   <form onSubmit={formik.handleSubmit}  className=''>
      <label>Forget Code</label>
      <input type="text" name='forgetCode' className='form-control my-3'
        value={formik.values.forgetCode}
        onChange={formik.handleChange}
      />
      {formik.errors.forgetCode?<p className="alert alert-danger">{formik.errors.forgetCode}</p>:''}
        
      <label>Email</label>
      
       <p className="text-danger">{statusError}</p>

      <input type="email" name='email' className='form-control my-3' 
       value={formik.values.email}
       onChange={formik.handleChange}
      />
      {formik.errors.email?<p className="alert alert-danger">{formik.errors.email}</p>:''}

      <label>password</label>
      <input type="password" name='password' className='form-control my-3' 
      value={formik.values.password}
      onChange={formik.handleChange}
      />
      {formik.errors.password? <p className='alert alert-danger'>{formik.errors.password}</p>:''}

      <label>cPassword</label>
      <input type="password" name='cPassword' className='form-control my-3'
       value={formik.values.cPassword}
       onChange={formik.handleChange}
      />
      {formik.errors.cPassword?<p className='alert alert-danger'>{formik.errors.cPassword}</p>:''}
      <button type='submit' className='btn btn-info mt-3'>Change Passowrd</button>
    </form>

   </div>
    
    </>
  )
}

