import axios from 'axios'
import { Formik, useFormik } from 'formik'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { object } from 'yup';
import * as Yup from 'yup';
import login from './Login.css';


export default function Login(props) {

  let [error,setError] = useState([]);
  let [statusError,setstatusError] = useState('');
  let navigate = useNavigate();
  const schema = Yup.object({
    email:Yup.string().required('email is required').email('not vallid email'),
    password:Yup.string().required('password is required'),
  });

  let {values,handleChange,handleSubmit,handleBlur,touched,errors} = useFormik({
    initialValues:{
      email:'',
      password:'',
    },validationSchema:schema,
    onSubmit:SendLoginData,

  })

  async function SendLoginData(values){
    let {data} = await axios.post('https://ecommerce-node4-five.vercel.app/auth/signin',values)
    .catch((err)=>{
      setstatusError(err.response.data.messgae);
    });
    if(data.message=='success'){
        setError([]);
        setstatusError('');
        localStorage.setItem('userToken',data.token);
        props.saveCurrentUser();
        navigate('/cart');
    }
    
     else{
        setError(data.err[0]);
    }


  }
  return (<>
    
    <div className='login'>

<div className='container d-flex'>


<div className=' w-50 my-5 logLift'>
  <h1>Welcome Back</h1>
  <p>Welcome back! Please enter your details</p>

  {error.map((err)=>{
    return <div className='alert alert-danger'> {err.message} </div>
  })}
<form onSubmit={handleSubmit}>
    <div className="w-50 my-4">
      <label className='form-label' htmlFor="">Email</label>
      <p className="text-danger">{statusError}</p>

      <input  placeholder='Enter your email ' name='email' type="email"
       value={values.email}
       onChange={handleChange}
       onBlur={handleBlur}
       className={`form-control ${errors.email && touched.email?'is-invalid':''}`}
      />
      {errors.email && touched.email?<div className='small text-danger'>{errors.email}</div>:''}
    </div>
    

    <div className="w-50 my-4">
      <label className='form-label' htmlFor="">Password</label>
      <input placeholder='******' name='password' type="password" 
        value={values.password}
        onChange={handleChange}
        onBlur={handleBlur}
        className={`form-control ${errors.password && touched.password?"is-invalid":""}`} 
      />
      {errors.password && touched.password?<div className='small text-danger'>{errors.password}</div>:''}
    </div>
    <Link className='forget' to='/sendCode'>Forget Password</Link>
    <div className='w-50 my-4'>
    <button type='submit'className='btn btn-primary form-control'>Sign in</button>
    </div>
  </form>

  <p className='pDont'>Don't have an account? <Link className='linkSingup ps-2' to='/register'>Sign up</Link> </p>

</div>

<div className='logRight'></div>



</div>

  </div>
    
    </>
  )
}

