import axios from "axios";
import { Formik, useFormik } from "formik";
import React, { useState } from "react";
import register from "./Register.css";
import { Link, useNavigate } from "react-router-dom";
import { object } from "yup";
import * as Yup from "yup";
import { toast } from "react-toastify";

export default function Register() {
  let [error, setError] = useState([]);
  let [statusError, setstatusError] = useState("");

  const [data, setData] = useState({
    userName: "",
    email: "",
    password: "",
    image: null,
  });

  let navigate = useNavigate();

  function handleChangeInput(e) {
    const { name, value } = e.target;
    setData((prev) => ({ ...prev, [name]: value }));
  }
  function handleChangeImage(e) {
    setData((prev) => ({ ...prev, image: e.target.files[0] }));
  }

  async function SendRegisterData(e) {
    e.preventDefault();
    const fd = new FormData();
    fd.append("userName", data.userName);
    fd.append("email", data.email);
    fd.append("password", data.password);
    fd.append("image", data.image);

    try {
      const { data } = await axios.post(
        "https://ecommerce-node4-five.vercel.app/auth/signup",
        fd
      );
      if (data.message == "success") {
        toast.success('We sent verification link to your email, please check your email')
        // console.log("hello");
        // setError([]);
        // setstatusError("");
        // navigate("/login");
      }
    } catch (error) {
      toast.error(error.response.data.message);
    }
  }
  return (
    <>
      <div className="container my-5 register d-flex">
        <div className="regLift w-50">
          <h1>Create your account</h1>
          <p className="pMember">
            Already A Member?{" "}
            <Link className="linkLog ms-2" to="/login">
              Log In
            </Link>
          </p>

          <div className="w-50 m-auto">
            {error.map((err) => {
              return <div className="alert alert-danger">{err.message}</div>;
            })}
          </div>

          <form className="" onSubmit={SendRegisterData}>
            <div className="w-50 my-4">
              <label htmlFor="userName" className="form-label">
                Name
              </label>
              <input
                className="form-control"
                placeholder="Enter your name "
                type="text"
                name="userName"
                onChange={handleChangeInput}
              />
            </div>

            <div className="w-50 my-4">
              <label htmlFor="email" className="form-label">
                Email address
              </label>
              <p className="text-danger">{statusError}</p>

              <input
                className="form-control"
                placeholder="Enter your email "
                type="email"
                name="email"
                aria-describedby="emailHelp"
                onChange={handleChangeInput}
              />
            </div>

            <div className="w-50 my-4">
              <label htmlFor="password" className="form-label">
                Password
              </label>
              <input
                className="form-control"
                placeholder="Enter your password "
                type="password"
                name="password"
                onChange={handleChangeInput}
              />
            </div>
            <div className="w-50 my-4">
              <label htmlFor="image" className="form-label">
                Profile Picture
              </label>
              <input
                className="form-control"
                placeholder="upload file"
                type="file"
                name="image"
                onChange={handleChangeImage}
              />
            </div>

            <div className="w-50 my-4">
              <button type="submit" className="btn btn-primary form-control">
                Create account
              </button>
            </div>
          </form>
        </div>

        <div className="regRight"></div>
      </div>
    </>
  );
}
