import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { CounterContext } from '../Context/CounterStore';
import { CartContext } from '../Context/CartStore';
import navbar from './Navbar.css';

export default function Navbar({user,logOut}) {
  const {number} = useContext(CounterContext);
  const {count} = useContext(CartContext);

  return (
   
  <nav className="navbar navbar-expand-lg bg-body-tertiary bg-dark py-3">
  <div className="container">
    <a className="navbar-brand text-white navEasy" href="#">EASY STORE {number}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active text-white" to='' >Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active text-white" to='products'>Products</Link>
        </li>

       {user?  <li className="nav-item">
          <Link className="nav-link text-white" to='cart'>Cart {count} </Link>
        </li>:''
               }
      </ul>

     

      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        {!user? <>
        
          <li className="nav-item">
          <Link className="nav-link text-white" to='login'>Login</Link>
        </li>

          <li className="nav-item">
          <Link className="nav-link text-white" to='register' >Register</Link>
        </li>
        </>:
        <>
          <li className="nav-item">
          <p className="nav-link text-white" onClick={logOut} >LogOut</p>
        </li>
        
        </>
        
        
        }

        
      </ul>
    </div>
  </div>
</nav>


  )
}
