import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { useLocation, useParams } from 'react-router-dom'
import { CartContext } from '../Context/CartStore';
import { toast } from 'react-toastify';

export default function ProductDetails() {

  let [product,setProduct] = useState({});
  let [image,setImage] = useState([]);
  const location = useLocation();
  let {id} = location.state;

  async function getProduct(){
    let {data} = await axios.get(`https://ecommerce-node4-five.vercel.app/products/${id}`);
    setProduct(data.product);
    setImage(data.product.subImages);
  }
  
  const {addToCart} = useContext(CartContext);

  async function addToCartFunc(productId,qty){
    if(localStorage.getItem('userToken')){
    const res = await addToCart(productId,qty);
    if(res.message == 'success'){
      toast.success("product added successfully");
    }
  }
  else{
    toast.error("login your account please");
  }

  }


  useEffect( ()=>{
    getProduct();

  },[])
  return (
    <>
<div className='my-4'>
    
    <h5>{product.name}</h5>
    <h3>All Images</h3>
    
    {image.map( (img)=>{
      return <img src={img.secure_url} className='w-50' alt="" />
    })
    }

      <button className='btn btn-success' onClick={ ()=>addToCartFunc(product._id)}>Add to cart</button>
      </div>
    </>
  )
}
