import axios from "axios";
import { createContext, useState } from "react";
import { Offline, Online } from "react-detect-offline";
import { toast } from "react-toastify";

export const CartContext = createContext(null);

export function CartContextProvider ({children}) {

    let [count,setCount] = useState(0)

    async function addToCart(productId,qty){

        try{    
            const token = localStorage.getItem('userToken');
            let objData = {
                productId,
                qty
            }
            console.log('this is cart',token);
            const {data} = await axios.post(`https://ecommerce-node4-five.vercel.app/cart/`,objData,
            {headers:{authorization:`Tariq__${token}`}});

            return data;
        }
        catch(error){
            console.log(error);
        }
    }

    async function getCart(){
        try{
            const token = localStorage.getItem('userToken');
            const {data} = await axios.get('https://ecommerce-node4-five.vercel.app/cart',
            {headers:{authorization:`Tariq__${token}`}});
            setCount(data.count);
            return data;

        }
        catch(error){
            console.log(error);
            toast.error('error')
         }
    }
    
    async function increaseQty(productId,quantity){
        try{
            let objData = {
                productId:productId
            }
            const token = localStorage.getItem('userToken');
            const {data} = await axios.patch(`https://ecommerce-node4-five.vercel.app/cart/incraseQuantity`,objData,
            {headers:{authorization:`Tariq__${token}`}});
            return data;
        }catch(error){
            console.log(error);
        }
    }

    async function decreaseQty(productId,quantity){
        try{
            let objData = {
                productId:productId
            }
            const token = localStorage.getItem('userToken');
            const {data} = await axios.patch(`https://ecommerce-node4-five.vercel.app/cart/decraseQuantity`,objData,
            {headers:{authorization:`Tariq__${token}`}});
            return data;
        }catch(error){
            console.log(error);
        }
    }

    async function deleteCart(productId){

       try{

        const token = localStorage.getItem('userToken');
        let objData = {
            productId:productId
        }
        const {data} = await axios.patch(`https://ecommerce-node4-five.vercel.app/cart/removeItem`,objData,
        {headers:{authorization:`Tariq__${token}`}});
        return data;
       }catch(error){
        console.log(error);
       }
    }

    return (
    
    <>
        
             <CartContext.Provider value={{addToCart,getCart,increaseQty,deleteCart,decreaseQty,count}}>
                {children};
             </CartContext.Provider>
        
    </>
    
    )
 
}
