import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { useProduct } from '../Hook/productHook';

export default function Products() {
    let products = useProduct();
    
  return (
    <>
    <div className="row">
        <div className="my-4"></div>
    {products.map( (product)=>{
      return <div className="col-md-4">
          <div className="products">
             <Link to={`/product/${product.slug}`} state={{id:product._id}}>
             <h3 className='mb-3'>{product.name}</h3>
              <span className='ms-3'>{product.price}</span>
              <img className='w-100' src={product.mainImage.secure_url} alt="" />
             </Link>
          </div>
        </div>
    })}
      </div>
      
    
    </>
  )
}
