import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../Context/CartStore'
import { toast } from 'react-toastify';
import cart from './Cart.css';


export default function Cart() {

  const {getCart,increaseQty,decreaseQty,deleteCart} = useContext(CartContext);
  const [products,setProducts] = useState([]);
  const [cart,setCart] = useState({});
  
  async function getCartFunc(){
    const res = await getCart();
    console.log('this is response',res);
    if(res.message == 'success'){
      setProducts(res.products);
    }
    else{
      toast.error('the cart is empity')
    }
  }

  async function increaseQtyFunc(productId,e){
    e.preventDefault();
    const res = await increaseQty(productId,e);
    getCartFunc();
  }

  async function decreaseQtyFunc(productId,e){
    e.preventDefault();
    const res = await decreaseQty(productId,e);
    getCartFunc();
  }

  async function deleteCartFunc(productId){
    let res = await deleteCart(productId);
    
    if(res.message == 'success'){
      toast.success("product delete successfully");
      getCartFunc();
    }
  }

  useEffect( ()=>{
    getCartFunc();
  },[])
  return (
    <>
    <div className='</div>'>
    <h2>Cart:</h2>
    <table className='table'>
      <tr>
        <td>Name</td>
        <td>Price</td>
        <td>Final Price</td>
        <td>Quantity</td>
        <td>Total Price</td>
        
      </tr>

     {products.map( (product)=>
       <tr className='py-3'>
       <td>{product.details.name}</td>
       <td>{product.details.price}</td>
       <td>{product.details.finalPrice}</td>
       <td>
       <button onClick={ (e)=>{increaseQtyFunc(product.productId,e)}} class="Qplus"><i class="fa-solid fa-circle-plus "></i></button>
          <input type="number" class="quantity"  disabled="disabled" step="1" value={product.quantity} name="quantity" min="1" max="9"></input>
          <button onClick={ (e)=>{decreaseQtyFunc(product.productId,e)}} class="Qplus Qminus"><i class="fa-solid fa-circle-minus "></i></button>
        </td>
       <td>{product.details.price * product.quantity }</td>
       
    
       <td onClick={ ()=>{deleteCartFunc(product.productId)}} className='btn btn-danger'>Delete</td>
       
     </tr>
     
     )}

     <tr className=''>
      <td colSpan="4">Total Price</td>
      
     </tr>
    </table>
    
    </div>
    </>
  )
}
