import axios from 'axios';
import { Formik, useFormik } from 'formik';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { object } from 'yup';
import * as Yup from 'yup';

export default function SendCode(props) {

  let [errors,setErrors] = useState([]);
  let [statusError,setstatusError] = useState('');
  let navigate = useNavigate();
  const schema = Yup.object({
    email:Yup.string().required('email is required').email('not vallid email'),
  });

  let formik = useFormik({
    initialValues:{
      email:'',
    },validationSchema:schema,
    onSubmit:forgetPassword,

  })

  async function forgetPassword(values){
    let {data} = await axios.patch('https://ecommerce-node4-five.vercel.app/auth/sendCode',values)
    .catch((err)=>{
      setstatusError(err.response.data.messgae);
    });
    if(data.message=='Done'){
        navigate('/ChangePassword');
        
    }
    
     else{
       setErrors(data.err[0])
    }


  }
  return (
    <>
    
   <div className="w-75 m-auto my-5">

    <h2>Send Code To Change Password</h2>
    
    {errors.map((error)=>{
     return <p className="alert alert-danger">{error.message}</p>
    })}

   <form onSubmit={formik.handleSubmit}  className=''>
  
        
      <label>Email</label>
      
       <p className="text-danger">{statusError}</p>

      <input type="email" name='email' className='form-control my-3' 
       value={formik.values.email}
       onChange={formik.handleChange}
      />
      {formik.errors.email?<p className="alert alert-danger">{formik.errors.email}</p>:''}

     
      <button type='submit' className='btn btn-info mt-3'>Send Code</button>


    </form>

   </div>
    
    </>
  )
}

