import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Slider from "react-slick";
import style from './Category.module.css'
import { useLocation } from 'react-router-dom';
export default function Category() {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3
      };
    let [categories,setCategories] = useState([]);
    let [subcategories,setSubCategories] = useState([]);
    let [categoriesIndex,setCategoriesIndex] = useState([]);

    async function getCategory(){
      const token = localStorage.getItem('userToken');

        let {data} = await axios.get("https://ecommerce-node4-five.vercel.app/categories",
        {headers:{authorization:`Tariq__${token}`}});
        console.log('sub',data.categories._id);

        setCategories(data.categories);
        console.log('this is use state',categories[0]);
    }
    
    async function getSubCategory(id){
      console.log('iddddd',id)
      let {data} = await axios.get(`https://ecommerce-node4-five.vercel.app/categories/${id}/subcategory/`);
      console.log('this is sub category',data)

      setSubCategories(data.subCategories);
    }

    //onClick={()=>{getSubCategory(category.id)}}

    useEffect( ()=>{
        getCategory();
    },[])

  return (
    <>
    
         <div className="container">

            <div className="row">
            <Slider {...settings}>

            {categories.map( (category)=>
                <div className="col-md-4" key={category.id}>
                  <div className="category text-center">
                    <img src={category.image.secure_url} alt={category.name} className={`${style.img} w-100`} 
                     />
                    
                    <p> {category.name} </p>
                  </div>
                </div>
            )}
              </Slider>
            </div>
         </div>


          <div className="mt-5">
            <div className="row">
            {subcategories.map( (category)=>{
                return <div className='col-md-3' key={category._id}>
                <div className="subCategory">
                  <p> {category.name} </p>
                  <img src={category.image.secure_url} className={`${style.img} w-100`} alt={category.name} />
                </div>
              </div>
              
              })}
            </div>
          </div>
        
    
    
    </>
  )
}
